<template>
  <div
    class="map"
    @mousemove="mousemove"
    @mouseup="mouseup"
    @click="checkDeselectBar"
  >
    <MapHalf
      :bars="sortedPositiveHalf"
      :top="true"
      ref="top"
    />
    <div class="centerline">
      <div class="centerlabel">Encounter</div>
      <div class="centerlabel">Explore</div>
      <div class="centerlabel">Engage</div>
      <div class="centerlabel">Embrace</div>
    </div>
    <MapHalf
      :bars="sortedNegativeHalf"
      :top="false"
    />
  </div>
</template>

<script>
import MapHalf from './MapHalf'

export default {
  components: { MapHalf },
  props: {
    bars: {
      type: Array,
      default: () => []
    },
  },
  data () {
    return {
      
    }
  },
  computed: {
    sortedPositiveHalf () {
      return this.bars.filter(b => b.positive !== false)
        .sort((a, b) => 
          a.start > b.start ?
            1
            : a.length < b.length ?
              1
              : -1
        )
    },
    sortedNegativeHalf () {
      return this.bars.filter(b => b.positive === false)
        .sort((a, b) => 
          a.start > b.start ?
            1
            : a.length < b.length ?
              1
              : -1
        )
    },
  },
  mounted () {
    if (window) window.addEventListener('resize', this.mapWidth)
    this.$nextTick(() => this.mapWidth())
  },
  methods: {
    mousemove (event) {
      if (this.$store.state.trackingMouseXFor)
        this.$store.commit('setMouseX', event.clientX)
    },
    mouseup (event) {
      if (this.$store.state.trackingMouseXFor)
        this.$store.commit('setTrackMouseX')
    },
    checkDeselectBar (event) {
      if (!(event.target ? event.target : event.path[0]).classList.contains('label') && !(event.target ? event.target : event.path[0]).classList.contains('barbutton'))
        this.$nextTick(() => this.$store.commit('selectBar'))
    },
    mapWidth () {
      let width
      try {
        width = this.$refs.top.$el.offsetWidth
        this.$store.commit('setMapWidth', width)
      } catch (e) {
        // console.log(this.$refs.top.$el, this.$refs.top, 'failed to get map width')
      }
    },
  }
}
</script>

<style lang="scss" scoped>

.map {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 100px;

  & > * {
    flex: 0;
    flex-grow: 0;
    flex-shrink: 0;
  }
}

.centerline {
  width: 100%;
  height: 40px;
  padding: 0px;
  margin: 25px 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 5px;
    right: 5px;
    height: 50%;
    border-bottom: 2px dashed rgba(black, .2);
    z-index: 1;
  }

  .centerlabel {
    position: relative;
    z-index: 2;
    color: rgba(black, .5);
    text-align: center;
    padding: 0 10px;
    background: white;
    user-select: none;
  }
}


</style>



