<template>
  <div
    class="half"
    :class="{ top }"
    @mouseover="mouseover"
    @mouseout="mouseout"
    @mousemove="mousemove"
    @click="click"
  >
    <Bar
      v-for="bar in shownBars"
      :key="bar.id"
      v-bind="bar"
    />
  </div>
</template>

<script>
import Bar from './Bar'

export default {
  components: { Bar },
  props: {
    bars: {
      type: Array,
      default: [],
    },
    top: {
      type: Boolean,
      default: true,
    }
  },
  data () {
    return {
      shownBars: this.bars,
      hovering: false,
    }
  },
  computed: {
    selectedBar () { return this.$store.state.selectedBar },
    mapWidth () { return this.$store.state.mapWidth },
  },
  watch: {
    bars (newBars) {
      this.shownBars = newBars
    }
  },
  methods: {
    mouseover (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedBar && !this.hovering) {
        this.hovering = true
        this.shownBars.push({
          start: this.getNewBarPosition(event.clientX - this.$el.offsetLeft),
          length: 12,
          id: 99999,
          positive: this.top,
          label: `New ${ this.top ? '' : 'Anti-' }Value`,
          ghost: true,
        })
      }
    },
    mousemove (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedBar) {
        if (this.hovering) {
          this.$set(
            this.bars[this.bars.length - 1], 
            'start', 
            this.getNewBarPosition(event.clientX - this.$el.offsetLeft)
          )
        }
        else
          this.mouseover(event)
      }
    },
    mouseout (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedBar && this.hovering) {
        this.hovering = false
        this.shownBars.pop()
      }
    },
    click (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedBar && !this.$store.state.trackingMouseXFor) {
        this.hovering = false
        this.shownBars.pop()
        this.$store.commit('addBar', {
          start: this.getNewBarPosition(event.clientX - this.$el.offsetLeft),
          length: 12,
          positive: this.top,
          label: `New ${ this.top ? '' : 'Anti-' }Value`,
        })
        this.$nextTick(() => {
          this.$nextTick(() => this.$store.commit('selectBar', this.$store.state.lastAddedBarId))
        })
      }
    },
    getNewBarPosition (mouseX) {
      let centeredX = mouseX - ((this.mapWidth / 100) * 6)
      let start = Math.round((centeredX / this.mapWidth) * 100)
      if ( start < 0) start = 0
      if (start > 88) start = 88
      return start
    }
  }
}
</script>

<style lang="scss" scoped>

.half {
  width: 100%;
  display: grid;
  grid-auto-flow: row dense;
  align-items: start;
  grid-template-columns: repeat(100, 1%);
  grid-template-rows: repeat(10, 37px);
  height: 400px;
  padding-bottom: 400px;
  cursor: cell;
  z-index: 10;

  &.top {
    transform: rotateX(180deg);

    & > * {
      transform: rotateX(180deg);
    }
  }

  
}

</style>



