<template>
  <div
    id="app"
  >
    <ControlBar
      :mapName="map ? map.name : null"
    />
    <ValueMap
      v-bind="map"
      v-if="map"
      class="flex"
    />
    <div id="help">
      Issues? Ideas? <a href="mailto:sec23615@adobe.com">Shoot an email to Jasper</a>
    </div>
  </div>
</template>

<script>
import ValueMap from './ValueMap'
import ControlBar from './ControlBar'

export default {
  components: { ValueMap, ControlBar },
  data () {
    return {
    }
  },
  computed: {
    map () { return this.$store.state.map },
  },
  mounted () {
    
  },
  methods: {
  }
}
</script>

<style lang="scss">
html, body {
  margin: 0;
}

* {
  box-sizing: border-box;
}

a, a:visited, a:active {
  color: #09f;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;

  .flex {
    flex: 1;
  }
}

#help {
  opacity: .5;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 7px 15px;
  z-index: 100;

  &:hover {
    opacity: 1;
  }
}

</style>



