import Vue from 'vue'
import Vuex from 'vuex'

const { get, set } = require('../assets/storage')

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		mapId: 0,
		map: loadMapFromStorage(),
		mouseXStart: -1,
		mouseXDifference: 0,
		trackingMouseXFor: null,
		mapWidth: 500,
		selectedBar: null,
		savedAt: null,
		lastAddedBarId: null,
		// undoStack: []
	},
	mutations: {
		setTrackMouseX (state, params) {
			if (params)
				state.mouseXDifference = 0
			state.mouseXStart =  params ? params.mouseXStart : null
			state.trackingMouseXFor = params ? params.for : null
		},
		setMouseX (state, mouseX) {
			if (state.trackingMouseXFor !== null)
				state.mouseXDifference = mouseX - state.mouseXStart
		},
		setMapWidth (state, width) {
			state.mapWidth = width
		},
		updateBar (state, newBar) {
			const foundBarIndex = state.map.bars.findIndex(bar => bar.id === newBar.id)
			if (foundBarIndex !== undefined)
				for (let param in newBar)
					Vue.set(state.map.bars[foundBarIndex], param, newBar[param])
			Vue.set(state.map, 'lastSaved', new Date())
			saveMap(state.map)
		},
		deleteBar(state, idToDelete) {
			const foundBarIndex = state.map.bars.findIndex(bar => bar.id === idToDelete)
			if (foundBarIndex !== undefined)
				Vue.delete(state.map.bars, foundBarIndex)
			Vue.set(state.map, 'lastSaved', new Date())
			saveMap(state.map)
		},
		selectBar (state, newBarId) {
			if (!newBarId)
				return state.selectedBar = null
			const foundBarIndex = state.map.bars.findIndex(bar => bar.id === newBarId)
			if (foundBarIndex !== undefined)
				state.selectedBar = newBarId
		},
		addBar (state, newBar) {
			newBar.id = Date.now()
			state.lastAddedBarId = newBar.id
			Vue.set(state.map.bars, state.map.bars.length, newBar)
			Vue.set(state.map, 'lastSaved', new Date())
			saveMap(state.map)
		},
		loadMap (state, newMap) {
			Vue.set(state, 'map', { ...newMap })
		},
		clearMap (state) {
			Vue.set(state, 'map', blankMap())
		},
		updateMapName (state, newName) {
			Vue.set(state.map, 'name', newName)
			Vue.set(state.map, 'lastSaved', new Date())
			saveMap(state.map)
		},
		// undo (state) {
		// 	if (state.undoStack.length === 0)
		// 		return
		// 	const newState = { ...state.undoStack.pop() }
		// 	Vue.set(state, 'map', { ...state.undoStack.pop() })
		// 	saveMap(state.map)
		//  Vue.set(state.map, 'lastSaved', new Date())
		// }
	},
})

function loadMapFromStorage ( id = 0, spawnIfNonexistant = false) {
	const loadedMaps = get('maps')
	if (loadedMaps && loadedMaps !== '') {
		let loadedMap = JSON.parse(loadedMaps)
		console.log(Object.keys(loadedMap).length)
		loadedMap = loadedMap[Object.keys(loadedMap)[0]]
		if (loadedMap)
			return loadedMap
	}
	if (spawnIfNonexistant)
		return spawnMap()
	return blankMap()
}

function saveMap ( map ) {
	let loadedMaps = get('maps')
	if (loadedMaps !== '')
		loadedMaps = JSON.parse(loadedMaps)
	if (loadedMaps === null || loadedMaps === '' || Array.isArray(loadedMaps))
		loadedMaps = {}
	loadedMaps[map.id] = map
	set('maps', JSON.stringify(loadedMaps))
}

function spawnMap () {
	const map = { bars: [], id: 0, name: 'My First Map', lastSaved: new Date() }
	for (let i = 0; i < 15; i++) {
		const start = Math.round(Math.random() * 100)
		let length = Math.round(Math.random() * 90) + 10
		if (start + length > 100) length = 100 - start
		map.bars.push({
			start,
			length,
			id: i + 1,
			// intensity: Math.round((Math.random() * 2) + 1),
			positive: Math.random() > .5,
			label: `${i + 1}`,
		})
	}
	return map
}

function blankMap () {
	return { bars: [], id: Date.now(), name: '(Click to Rename)', lastSaved: new Date() }
}



