<template>
  <div
    class="baroptions"
		:class="{
			active,
		}"
  >
  <div
	 	@click="$emit('crescendo')"
		class="barbutton"
		:class="{ active: crescendo }"
	></div>
	<div
	 	@click="$emit('decrescendo')"
		class="barbutton"
		:class="{ active: decrescendo }"
	></div>
	<div
	 	@click="$emit('delete')"
		class="barbutton"
	></div>
  </div>
</template>

<script>

export default {
  props: {
    active: {
			type: Boolean,
			default: false,
		},
		crescendo: {
			type: Boolean,
			default: false,
		},
		decrescendo: {
			type: Boolean,
			default: false,
		},
  },
  data () {
    return {
      
    }
  },
  computed: {
    
  },
  mounted () {
    
  },
  methods: {
   
  }
}
</script>

<style lang="scss" scoped>

.baroptions {
	-webkit-transform:translate3d(0,0,0);
	position: absolute;
	left: 12px;
	z-index: 13;
	top: 100%;
	margin-top: -2px;
	background: #444;
	color: white;
	border-bottom-right-radius: 7px;
	border-bottom-left-radius: 7px;
	opacity: 0;
	pointer-events: none;
	transition: 0s;
	display: flex;
	overflow: hidden;
	user-select: none;

	&.active {
		transition: all .2s;
		opacity: 1;
		pointer-events: auto;
	}

	& > * {
		padding: 5px 5px;
		min-width: 30px;
		text-align: center;
		
		&:hover {
			background: #666;
		}

		&.active {
			background: #09f;
			color: white;
		}
	}
}

</style>



