<template>
  <div
    class="bar"
    :class="{
      fade: selectedBar && !isSelected,
      ghost,
      selected: isSelected,
    }"
    :style="{
      'grid-column': (displayStart + 1) + '/' + (displayEnd + 1),
      left: dragDistance + 'px',
      'margin-right': (-1 * resizeDistance) + 'px'
    }"
  >
    <div
      class="bg"
      :class="{
      positive,
      negative: !positive,
      selected: isSelected,
      crescendo,
      decrescendo,
    }"
    ></div>
    <div
      class="label"
      :class="{
        dragging,
        editing: isSelected && !dragging && !resizing,
      }"
      ref="label"
      :contenteditable="isSelected && !dragging && !resizing"
      @mousedown="mousedownLabel"
      @mouseup="mouseupLabel"
      @input="updateText"
      @keydown.enter="enterKey"
      @keydown.delete="deleteKey"
      v-html="displayLabel"
    ></div>
    <div
      class="resizer"
      :class="{
        on: resizing
      }"
      v-if="!isSelected || resizing"
      @mousedown="mousedownResize"
      @mouseup="mouseupResize"
    >||</div>

    <BarOptions
      :active="isSelected && !dragging && !resizing"
      :crescendo="crescendo"
      :decrescendo="decrescendo"
      @delete="deleteBar"
      @crescendo="toggleCrescendo"
      @decrescendo="toggleDecrescendo"
    />
  </div>
</template>

<script>
import BarOptions from './BarOptions'

export default {
  components: { BarOptions, },
  props: {
    positive: {
      type: Boolean,
      default: true,
    },
    start: {
      type: Number,
      default: 0,
    },
    length: {
      type: Number,
      default: 15,
    },
    label: {
      type: String,
      default: '',
    },
    intensity: {
      type: Number,
      default: 2,
    },
    id: {
      type: Number,
      required: true,
    },
    ghost: {
      type: Boolean,
      default: false,
    },
    crescendo: {
      type: Boolean,
      deafult: false,
    },
    decrescendo: {
      type: Boolean,
      deafult: false,
    }
  },
  data () {
    return {
      dragging: false,
      resizing: false,
      displayLabel: this.label,
    }
  },
  computed: {
    selectedBar () { return this.$store.state.selectedBar },
    isSelected () { return this.selectedBar === this.id },
    trackingMouseXForBarDrag () { return this.$store.state.trackingMouseXFor === 'barDrag' },
    trackingMouseXForResize () { return this.$store.state.trackingMouseXFor === 'resize' },
    dragDistance () {
      if (!this.dragging || this.$store.state.trackingMouseXFor !== 'barDrag') return 0
      return this.$store.state.mouseXDifference
    },
    resizeDistance () {
      if (!this.resizing || this.$store.state.trackingMouseXFor !== 'resize') return 0
      return this.$store.state.mouseXDifference
    },
    displayStart () {
      return this.start <= 0
        ? 0
        : this.start + this.length >= 100
          ? 100 - this.length
          : this.start
    },
    displayEnd () {
      return this.start <= 0
        ? this.length
        : this.start + this.length >= 100 ? 100 :
        this.start + this.length
    },
  },
  watch: {
    trackingMouseXForBarDrag (isTracking) {
      if (!isTracking && this.isSelected) {
        this.mouseupLabel()
      }
    },
    trackingMouseXForResize (isTracking) {
      if (!isTracking && this.isSelected) {
        this.mouseupResize()
      }
    },
    isSelected (isSelected) {
      if (!isSelected && this.$refs.label.innerHTML === '')
        this.$store.commit('deleteBar', this.id)
      if (isSelected && !this.dragging && !this.resizing)
        this.$nextTick(() => this.selectText())
    }
  },
  methods: {
    updateText (event) {
      const textToSave = event.target.innerText
      this.$store.commit('updateBar', {
        id: this.id,
        label: textToSave
      })
    },
    enterKey (event) {
      event.preventDefault()
      return this.$store.commit('selectBar')
    },
    deleteKey (event) {
      if (event.srcElement.innerHTML === '') {
        this.$store.commit('deleteBar', this.id)
        this.$store.commit('selectBar')
      }
    },
    mousedownResize (event) {
      this.resizing = true
      this.$store.commit('selectBar', this.id)
      this.$store.commit('setTrackMouseX', {
        for: 'resize',
        mouseXStart: event.clientX,
      })
    },
    mouseupResize () {
      const mapWidth = this.$store.state.mapWidth
      let resizeAsPercent = Math.round((this.$store.state.mouseXDifference / mapWidth) * 100)
      if (this.length + resizeAsPercent < 3)
        resizeAsPercent = (this.length * -1) + 3
      if (this.length + resizeAsPercent + this.start > 100)
        resizeAsPercent = 100 - this.length - this.start
      this.$store.commit('updateBar', { 
        id: this.id, 
        length: this.length + resizeAsPercent
      })

      this.resizing = false
      this.$store.commit('setTrackMouseX')
      this.$store.commit('selectBar')
    },
    mousedownLabel (event) {
      if (this.isSelected)
        return
      this.$store.commit('selectBar', this.id)
      this.dragging = true
      this.$store.commit('setTrackMouseX', {
        for: 'barDrag',
        mouseXStart: event.clientX,
      })
    },
    mouseupLabel () {
      if (this.dragging === false) return
      this.dragging = false
      const mapWidth = this.$store.state.mapWidth
      const shiftAsPercent = Math.round((this.$store.state.mouseXDifference / mapWidth) * 100)
      if (shiftAsPercent === 0) {
        this.$nextTick(() => this.selectText())
      }
      else {
        const toUpdate = {
          id: this.id,
          start: this.start + shiftAsPercent,
        }
        if (this.start + shiftAsPercent > 97)
          toUpdate.start = 97
        if (this.start + shiftAsPercent < 0)
          toUpdate.start = 0
        if (this.start + shiftAsPercent + this.length > 100)
          toUpdate.length = 100 - (this.start + shiftAsPercent)
        this.$store.commit('updateBar', toUpdate)
        this.$store.commit('selectBar')
      }

      this.$store.commit('setTrackMouseX')
    },
    selectText () {
      const labelElement = this.$el.querySelector('.label')
      labelElement.focus()
      if (document.body.createTextRange) {
        const range = document.body.createTextRange()
        range.moveToElementText(labelElement)
        range.select()
      }
      else if (window.getSelection) {
        const selection = window.getSelection()
        const range = document.createRange()
        range.selectNodeContents(labelElement)
        selection.removeAllRanges()
        selection.addRange(range)
      }
    },
    deleteBar () {
      this.$store.commit('deleteBar', this.id)
      this.$store.commit('selectBar')
    },
    toggleCrescendo () {
      this.$store.commit('updateBar', {
        id: this.id,
        crescendo: !this.crescendo
      })
    },
    toggleDecrescendo () {
      this.$store.commit('updateBar', {
        id: this.id,
        decrescendo: !this.decrescendo
      })
    },
  }
}
</script>

<style lang="scss" scoped>

.bar {
  position: relative;
  padding: 2px;
  transition: box-shadow .2s, opacity .2s;
  z-index: 13;
  cursor: pointer;

  &.selected {
    z-index: 15;
    -webkit-transform:translate3d(180,0,0);
  }

  :focus {
    outline: 0;
  }

  &.fade {
    opacity: .5;
    z-index: 9;
  }

  &.ghost {
    opacity: .2;
    pointer-events: none;
  }

  &:hover {
    .resizer { opacity: 1; }
  }

  .bg {
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    border-radius: 7px;
    z-index: 12;
    // opacity: .8;

    &.positive {
      background: #00d0a4;
    }
    &.positive:before, &.positive:after {
      border-bottom: 33px solid #00d0a4;
    }

    &.negative {
      background: #f17b5f;
    }
    &.negative:before, &.negative:after {
      border-top: 33px solid #f17b5f;
    }

    &.selected {
      box-shadow: 0 3px 7px rgba(black, .4);
      z-index: 14 !important;
      opacity: 1;
    }

    &:before, &:after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      z-index: 15;
    }

    &.crescendo {
      &:before {
        left: -14px;
        transform: translateX(-15px);
        border-left: 33px solid transparent;
      }
    }
    &.decrescendo {
      &:after {
        right: -14px;
        transform: translateX(15px);
        border-right: 33px solid transparent;
      }
    }
  }

  .label {
    position: relative;
    font-size: .9em;
    font-weight: 600;
    color: white;
    user-select: none;
    width: 100%;
    padding: 7px 10px;
    white-space: nowrap;
    overflow: hidden;
    z-index: 15 !important;
    cursor: pointer;
    user-select: none;

    &.dragging {
      cursor: grab;
    }
    &.editing {
      cursor: text;
      user-select: auto;
    }

    &::selection {
      background: rgba(#000, .4);
    }
  }

  .resizer {
    position: absolute;
    z-index: 15;
    opacity: 0;
    cursor: ew-resize;
    width: 25px;
    right: 0;
    top: 13%;
    height: 100%;
    color: rgba(white, .7);
    font-size: 1.4rem;
    padding-left: 5px;
    user-select: none;

    &:hover, &.on {
      opacity: 1;
    }
  }
}



</style>



