<template>
  <div class="controlbar">
    <h1
      ref="mapname"
      :contenteditable="this.mapName !== undefined"
      :class="{
        editable: this.mapName !== undefined,
      }"
      @keydown.enter="deselect"
      @input="updateMapName"
    >
    {{ this.mapName !== undefined ? `${ this.nameToDisplay }` : 'Value / Anti-Value Map Creator' }}
    </h1>
    <div v-if="lastSaved" class="saved">Saved at {{ lastSaved.toLocaleTimeString() }}</div>
    <div class="buttons">
      <!--<span @click="inputtingJSON = true">Paste JSON</span>
      <span @click="copyJSON">{{ copyJSONButtonText }}</span>
      <span v-if="undoAvailable" @click="undo">Undo</span>-->
      <span @click="clearMap">Clear Map</span>
      <span @click="copyLink">{{ copyLinkButtonText }}</span>
      <span @click="exportPNG">Export PNG</span>
    </div>
    <div
      v-if="inputtingJSON"
      class="takeover"
      @click="closeIfNotTextarea"
    >
      <div
        v-if="inputErrorMessage"
        class="errormessage"
      >{{ inputErrorMessage }}</div>
      <textarea
        class="jsoninput"
        ref="jsoninput"
        v-model="json"
        @keydown.escape="inputtingJSON = false"
      ></textarea>
      <div class="submitbutton" @click="loadMap">
        Load it!
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
import LZString from './assets/lz-string'

export default {
  components: {  },
  props: {
    mapName: {}
  },
  data () {
    return {
      inputtingJSON: false,
      json: '',
      copyJSONButtonText: 'Copy JSON',
      copyLinkButtonText: 'Copy Link',
      nameToDisplay: this.mapName,
      inputErrorMessage: null,
    }
  },
  computed: {
    lastSaved () { return this.$store.state.map.lastSaved },
    map () { return this.$store.state.map },
    // undoAvailable () { return this.$store.state.undoStack.length > 0 },
  },
  watch: {
    inputtingJSON (opening) {
      if (opening)
        this.$nextTick(() => this.$refs.jsoninput.focus())
    },
    map (newMap) {
      this.nameToDisplay = newMap.name
    }
  },
  mounted () {
    this.checkURLHash()
  },
  methods: {
    // undo () {
    //   this.$store.commit('undo')
    // },
    deselect () {
      this.$refs.mapname.blur()
    },
    updateMapName (event) {
      const textToSave = event.target.innerText
      this.$store.commit('updateMapName', textToSave)
    },
    checkURLHash () {
      if (!window.location.hash) return
      const newMap = this.decompressMap(window.location.hash.substring(1))
      if (typeof newMap === 'object')
        this.$store.commit('loadMap', newMap)
      window.location.hash = ''
    },
    copyLink () {
      const baseURL = window.location.href.replace(/#.*$/, '')
      const compressedMap = this.compressMap(JSON.stringify(this.$store.state.map))
      this.copyText(`${ baseURL }#${ compressedMap }`)
      this.copyLinkButtonText = 'Copied!'
      setTimeout(() => this.copyLinkButtonText = 'Copy Link', 2000)
    },
    loadMap () {
      let parsedJSON
      try { parsedJSON = JSON.parse(this.json) }
      catch (e) { this.inputErrorMessage = 'Invalid JSON object. Try again!' }
      if (parsedJSON) {
        this.inputtingJSON = false
        this.$store.commit('loadMap', parsedJSON)
        this.inputErrorMessage = null
      }
    },
    clearMap () {
      if (confirm("Are you sure you want to erase this map?"))
        this.$store.commit('clearMap')
    },
    exportPNG () {
      const element = document.querySelector('.map')
      html2canvas(element).then(canvas => {
        const png = canvas.toDataURL('image/png')
        const id = this.$store.state.mapId
        this.download(png, '', `${ this.mapName }.png`, true)
      })
    },
    copyJSON () {
      this.copyText(JSON.stringify(this.$store.state.map))
      this.copyJSONButtonText = 'Copied!'
      setTimeout(() => this.copyJSONButtonText = 'Copy JSON', 2000)
    },
    copyText (text) {
      const el = document.createElement('textarea')
      el.value = text
      document.body.appendChild(el)
      el.select()
      document.execCommand('copy')
      document.body.removeChild(el)
    },
    exportJSON () {
      const content = JSON.stringify(this.$store.state.map)
      const id = this.$store.state.mapId
      this.download(content, 'text/plain', `${ this.mapName }.json`)
    },
    download (content, type, fileName, isURI = false) {
      var a = document.createElement("a")
      var file = isURI ? content : new Blob([content], { type })
      a.href = isURI ? file : URL.createObjectURL(file)
      a.download = fileName
      a.click()
    },
    closeIfNotTextarea (event) {
      if (!event.path[0].classList.contains('jsoninput') && !event.path[0].classList.contains('submitbutton'))
        this.inputtingJSON = false
    },
    compressMap (map) {
      let stringMap = map
      stringMap = JSON.stringify(map)
      stringMap = LZString.compressToEncodedURIComponent(stringMap)
      return stringMap
    },
    decompressMap (compressedMap) {
      let newMap = LZString.decompressFromEncodedURIComponent(compressedMap)
      try {
        newMap = JSON.parse(JSON.parse(newMap))
        return newMap
      }
      catch (e) { return null }
      
    }
  }
}
</script>

<style lang="scss" scoped>

.controlbar {
  position: relative;
  z-index: 100;
  padding: 0 30px;
  width: 100vw;
  // background: white;
  // border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;

  h1 {
    font-size: 1.8rem;
    font-weight: 600;

    &.editable {
      cursor: text;
    }
  }

  .saved {
    margin-left: 20px;
    opacity: .3;
  }

  .buttons {
    flex: 1;
    text-align: right;
    
    & > * {
      cursor: pointer;
      transition: all .2s;
      margin-left: 5px;
      border-radius: 7px;
      padding: 7px 10px;
      background: #eee;

      &:hover {
        background: #09f;
        color: white;
      }
    }
  }

  .takeover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(black, .7);
    display: flex;
    flex-direction: column;
    padding: 20vh 20vw;
    align-items: flex-end;
    justify-content: center;

    .errormessage {
      width: 100%;
      background: #d90;
      color: white;
      margin-bottom: 10px;
      padding: 10px 15px;
      border-radius: 7px;
    }

    .jsoninput {
      flex: 1;
      width: 100%;
      border-radius: 7px;
      padding: 15px;
    }
    .submitbutton {
      margin-top: 10px;
      border-radius: 7px;
      background: #09f;
      color: white;
      padding: 7px 10px;
      cursor: pointer;
    }
  }
}

</style>



